* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter";
  font-weight: 300;
}

#root {
  height: 100vh;
  width: 100vw;
  position: relative;
  background: repeating-linear-gradient(30deg, #333, #333 20px, #222 20px, #222 40px);
}

.container {
  position: relative;
  max-height: 90vh;
  max-width: 800px;
  transform: translateX(-50%) translateY(-50%);
  left: 50%;
  top: 50%;
  border: solid 2px white;
}
